import React, { useState } from "react";

export const Tour = ({id, image, name, info, price, removeTour}) => {
  const [LessData, setLessData] = useState(false);
  return (
    <article className="single-tour" key={id}>
      <img src={image} alt={name} />
      <footer>
        <div className="tour-info">
          <h4>{name}</h4>
          <h3 className="tour-price">NRS {price}</h3>
        </div>
        <p>
          {LessData ? info : `${info.substring(0, 100)}... `}
          <span className="read-more" onClick={() => setLessData(!LessData)}>
            {LessData ? " Show Less" : "Read More"}
          </span>
        </p>
        <button className="delete-btn" onClick={() => removeTour(id)}>
          not Interested
        </button>
      </footer>
    </article>
  );
};
