import React from "react";
import { Tour } from "./Tour";


export const Tours = ({toursProps,removeTour}) => {
  return (
    <section>
      <div className="title">
        <h2>Our Tours</h2>
      </div>
      <div>
        {toursProps.map((item) => {
          return <Tour key={item.id} {...item} removeTour={removeTour} />;
        })}
      </div>
    </section>
  );
};
