import { useEffect, useState } from "react";
import "./App.css";
import { Loading } from "./Loading";
import { Tours } from "./Tours";
const url = "https://course-api.com/react-tours-project";

function App() {
  const [loading, setLoading] = useState(false);
  const [tours, setTours] = useState([]);

  const removeTour = (id) => {
    const newList = tours.filter((item) => item.id !== id);
    setTours(newList);
  };

  const fetchTours = async () => {
    setLoading(true)
    try {
      const response = await fetch(url);
      const tourData = await response.json();
      setLoading(false);
      setTours(tourData);
    } catch (err) {
      setLoading(false);
      console.log("There is error in>>", err);
    }
  };
  useEffect(() => {
    fetchTours();
  }, []);

  if (loading) {
    return (
      <main>
        <Loading />
      </main>
    );
  }
  if (tours.length === 0) {
    return (
      <div className="no-tours-left">
        <h3>No Tours Left</h3>
        <button className="refresh" onClick={fetchTours}>
          Refresh
        </button>
      </div>
    );
  }
  return (
    <div className="App">
      <Tours toursProps={tours} removeTour={removeTour} />
    </div>
  );
}

export default App;
